.header {
  height: 60px;
  display: flex;
  align-items: center;
  background-color: #131921;
  position: sticky;
  top: 0;
  z-index: 100;
}

.HeaderLogo {
  width: 100px;
  object-fit: contain;
  margin: 0 20px;
  margin-top: 18px;
}
.headerSearch{
  display: flex;
  flex: 1;
  align-items: center;
  border-radius: 24px;
}
.headerSearchInput{
  height: 12px;
  padding: 10px;
  border: none;
  width: 100%;
}
.headerSearchIcon{
  padding: 2px;
  height: 22px !important;
  background-color: #cd9042;
}
.headerNav {
  display: flex;
  justify-content: space-evenly;
}
.headerOption {
  display: flex;
  flex-direction: column;
  margin-left: 10px;
  margin-right: 10px;
  color: white;
}
.headerOptionLineOne {
  font-size: 10px;
}
.headerOptionLineTwo{
  font-size: 13px;
  font-weight: 800;
}
.headerOptionBasket {
  display: flex;
  align-items: center;
  color: white;
}
.headerBasketCount {
  margin-left: 10px;
  margin-right: 10px;
}
.headerSearchIcon {
  padding: 5px;
}