.product {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    margin: 10px;
    padding: 20px;
    width: 100%;
    max-height: 400px;
    min-width: 100px;
    background-color: white;
    z-index: 1;
}
.product > img{
    max-height: 200px;
    width: 100%;
    object-fit: contain;
    margin-bottom: 15px;
}
.product > button {
    background-color: #f0c14b;
    border: 1px solid;
    margin-top: 10px;
    border-color:#a88734 #9cde31 #846a29 ;
    color: #111;
}
.productInfo {
    height: 100px;
    margin-bottom: 5px;
}
.productPrice {
    margin-top: 5px;
}